<template>
    <div class="bscroll-wrapper" ref="wp">
             <div class="bscroll-connent" ref="con">
                 <div class="pulldown-scroller" v-show="pullDown" >
                    <div class="pulldown-wrapper" >
                    <div v-show="beforePullDown">
                        <span>下拉刷新</span>
                    </div>
                    <div v-show="!beforePullDown">
                        <div v-show="isPullingDown">
                         <span>{{msg}}</span>
                        </div>
                        <div v-show="!isPullingDown">
                        <span>刷新完毕</span>
                        </div>
                    </div>
                   </div>
                </div>
               <ul ref="slot" :class="type==='y'?'bscroll-listy':'bscroll-listx'">
                  <slot />
               </ul>
                 <div class="pullup-tips"  v-show="pullUpLoad">
                    <div v-if="!isPullUpLoad" class="before-trigger">
                        <span class="pullup-txt">{{pullup}}</span>
                    </div>
                    <div v-else class="after-trigger">
                        <span class="pullup-txt">{{msg}}</span>
                    </div>
                 </div>
       </div>
    </div>
</template>

<script>
import BScroll from '@better-scroll/core'
import PullDown from '@better-scroll/pull-down'
import Pullup from '@better-scroll/pull-up'
import ScrollBar from '@better-scroll/scroll-bar'
import InfinityScroll from '@better-scroll/infinity'
import MouseWheel from '@better-scroll/mouse-wheel'
import NestedScroll from '@better-scroll/nested-scroll'
BScroll.use(NestedScroll)
BScroll.use(MouseWheel)
BScroll.use(InfinityScroll)
BScroll.use(PullDown)
BScroll.use(Pullup)
BScroll.use(ScrollBar)
export default {
  props: [
    'pullDown',
    'type',
    'pullUpLoad',
    'upload',
    'loadtime',
    'ScrollBar',
    'click',
    'mouseWheel',
    'onscroll',
    'nestedScroll'
  ],
  data() {
    return {
      bscroll: null,
      beforePullDown: true,
      isPullingDown: false,
      isPullUpLoad: false,
      msg: 'Loading',
      pullup: '',
      time: null,
      conH: 0,
      pageNum: 0,
      nextItem: 0
    }
  },
  mounted() {
    this.conW()
    this.init()
    this.conH = this.$refs.con.offsetHeight
  },
  methods: {
    init() {
      this.bscroll = new BScroll('.bscroll-wrapper', {
        pullDownRefresh: this.pullDown || false,
        scrollY: this.type === 'y' ? true : false,
        scrollX: this.type === 'x' ? true : false,
        pullUpLoad: this.pullUpLoad || false,
        scrollbar: this.ScrollBar || true,
        click: this.click || false,
        preventDefault: false,
        probeType: 3,
        mouseWheel: this.mouseWheel || false,
        nestedScroll: this.nestedScroll || false
      })
      this.pullUpLoad && this.bscroll.on('pullingDown', this.pullingDownHandler)
      this.pullDown && this.bscroll.on('pullingUp', this.pullingUpHandler)
      this.bscroll.on('scroll', this.scrollHandler)
      this.innerScroll = new BScroll(this.$refs.innerScroll, {
        nestedScroll: true,
        bounce: {
          top: false,
          bottom: false
        }
      })
      this.bscroll.on('scrollEnd', e => {})
    },
    scrollHandler(pos) {
      this.conH = this.$refs.con.offsetHeight
      this.onscroll && this.onscroll(pos)
    },
    conW() {
      if (this.type === 'x') {
        let uls = [...this.$refs.slot.children]
        let str = 0
        uls.forEach(e => {
          str += e.offsetWidth
        })
        this.$refs.con.style.width = str + 'px'
      }
    },
    async pullingDownHandler() {
      this.beforePullDown = false
      this.isPullingDown = true
      this.loading()
      setTimeout(() => {
        this.isPullingDown = false
        this.finishPullDown()
      }, 1000)
    },
    finishPullDown() {
      location.reload()
    },
    async pullingUpHandler() {
      this.pullup = '上拉加载'
      this.isPullUpLoad = true
      this.loading()
      setTimeout(async () => {
        this.time = null
        this.upload && (await this.upload())
        this.bscroll.finishPullUp()
        this.bscroll.refresh()
        this.isPullUpLoad = false
        if (this.$refs.con.offsetHeight === this.conH) {
          this.pullup = '没有更多了'
        } else {
          this.pullup = ''
        }
      }, this.loadtime || 2000)
    },
    loading() {
      let i = 0
      this.time = setInterval(() => {
        let str = 'Loading'
        for (let a = 0; a < i; a++) {
          str += '.'
        }
        this.msg = str
        i++
        if (i > 4) {
          i = 0
        }
      }, 400)
    }
  }
}
</script>

<style lang="scss" >
.bscroll-wrapper {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
}

.disn {
  display: none;
}

.bscroll-listx {
  display: flex;
  flex-wrap: nowrap;
  li,
  div,
  span,
  p {
    padding: 0 10px;
    flex-shrink: 0;
    float: left;
  }
}

.pulldown-wrapper {
  position: absolute;
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
  transform: translateY(-100%) translateZ(0);
  text-align: center;
  background: #e8e8e8;
  color: #999;
}
.pullup-tips {
  padding: 20px;
  text-align: center;
  color: #e8e8e8;
  font-size: 20px;
}
.bscroll-vertical-scrollbar {
  height: 100%;
}
</style>